import React, { useState } from 'react';

import { Navbar } from '@multiverses/verse-react/src/components/Navigation/Navbar';
import { Route, Routes } from '@multiverses/verse-react/src/components/Route';
import { SideBar } from '@multiverses/verse-react/src/components/SideBar';

import { APOD } from './APOD';

export const mainTools = [
  {
    name: 'APOD',
    icon: 'home',
    to: '',
    end: true,
  },
];

export const Home = () => {
  const [showSideNav, setShowSideNav] = useState(true);

  const onMenuClick = () => {
    setShowSideNav(!showSideNav);
  };

  return (
    <React.Fragment>
      <div className="abs-right">
        <Navbar
          className="fdr-reverse"
          onMenuClick={onMenuClick}
          showMenu={showSideNav}
        />
      </div>
      <div className="flex">
        <div className="flex-9">
          <div className="m-4 p-4">
            <Routes>
              <Route element={<APOD />} path="/" />
            </Routes>
          </div>
        </div>
        <SideBar contents={mainTools} show={showSideNav} />
      </div>
    </React.Fragment>
  );
};
