import { Card } from '@multiverses/verse-react/src/components/Card';
import PropTypes from 'prop-types';
import { NavLink, Route, Routes } from 'react-router-dom';

export const CardsPage = ({ cards }) => {
  return (
    <>
      <For each="card" index="idx" of={cards}>
        <If condition={!card.hide}>
          <Routes key={card.name}>
            <Route
              element={
                <div className="r-flex-10">
                  <Card className={`p-16 m-8`} key={idx}>
                    <NavLink to={card.name.replace(/\s/g, '').toLowerCase()}>
                      <div className="m-8 p-16 secondary">
                        <b className="navitem-title">{card.name}</b>
                      </div>
                    </NavLink>
                    <div className="m-8 p-4">{card.description}</div>
                  </Card>
                </div>
              }
              path={`/`}
            />
            <Route
              element={card.component}
              path={`/${card.name.replace(/\s/g, '').toLowerCase()}/*`}
            />
          </Routes>
        </If>
      </For>
    </>
  );
};

CardsPage.propTypes = {
  cards: PropTypes.array,
};

CardsPage.defaultProps = {
  cards: [],
};
