import { useEffect } from 'react';

import { useNasaApi } from '../../../../hooks/Api/useNasaApi';

export const APOD = () => {
  const { apodState, apodRequest } = useNasaApi('apod');
  const { apod = {} } = apodState;

  useEffect(() => {
    apodRequest();
  }, []);

  return (
    <div>
      <div className="flex center">
        <div className="r-flex-12">
          <div className="t-center">
            <details>
              <summary>
                {apod.title} ({apod.date})
              </summary>
              <p>{apod.explanation}</p>
            </details>
          </div>
          <div className="m-4 p-4">

          </div>
          <Choose>
            <When condition={apod.media_type === 'video'}>
              <iframe
                allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture"
                allowFullScreen
                frameBorder="0"
                height="500"
                src={apod.url}
                width="1100"
              ></iframe>
            </When>
            <Otherwise>
              <img src={apod.url} />
            </Otherwise>
          </Choose>
        </div>
      </div>
    </div>
  );
};
